<!--
 * @Descripttion: 数据字典管理页面
 * @version:
 * @Author: ZJM
 * @Date: 2020-04-14 10:09:28
 * @LastEditors: ZJM
 * @LastEditTime: 2021-05-12 17:34:40
 -->
<template>
  <div>
    <div v-show="showPage === 0">
      <el-container class="content">
        <el-header>
          <HeaderTitle class="header-title">
            <div slot="head-search" class="head-search">
              <el-form
                ref="tableForm"
                :inline="true"
                :model="formInline"
                class="demo-form-inline"
                size="mini"
              >
                <el-form-item :label="PLC_PARAMS.ZH_NAME + '：'">
                  <el-input
                    v-model="formInline.cnName"
                    :placeholder="PLC_PARAMS.ZH_NAME"
                  ></el-input>
                </el-form-item>
                <el-form-item :label="PLC_PARAMS.PARAMETER_NAME + '：'">
                  <el-input
                    v-model="formInline.parameterName"
                    :placeholder="PLC_PARAMS.PARAMETER_NAME"
                  ></el-input>
                </el-form-item>
                <el-form-item :label="PLC_PARAMS.SHORT_NAME + '：'">
                  <el-input
                    v-model="formInline.shortName"
                    :placeholder="PLC_PARAMS.SHORT_NAME"
                  ></el-input>
                </el-form-item>
                <el-form-item :label="PLC_PARAMS.IS_WARNING + '：'">
                  <el-select
                    v-model="formInline.isWarning"
                    :placeholder="PLC_PARAMS.IS_WARNING"
                    class="inputWidth"
                  >
                    <el-option :label="VARIABLE.ALL" value="null"></el-option>
                    <el-option :label="VARIABLE.YES" value="1"></el-option>
                    <el-option :label="VARIABLE.NO" value="0"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="findList">{{
                    BTN_TEXT.SEARCH
                  }}</el-button>
                  <el-button @click="onReset">{{ BTN_TEXT.RESET }}</el-button>
                </el-form-item>
              </el-form>
            </div>
          </HeaderTitle>
        </el-header>
        <el-main>
          <div class="area-main">
            <div class="area-aside">
              <!-- ztree组件 -->
              <HeaderTitle
                class="header-title"
                :titleName="PLC_PARAMS.DICTIONARY_TEMPLATE"
              ></HeaderTitle>
              <ZTree
                :nodes="nodeData"
                @ztreeOnClick="ztreeOnClick"
                @ztreeOnRightClick="ztreeOnRightClick"
              ></ZTree>
            </div>
            <div class="area-info">
              <template>
                <div class="userInformation">
                  <HeaderTitle
                    class="header-title"
                    :titleName="PLC_PARAMS.PARAMS_LIST"
                  >
                    <div slot="head-search" class="head-search">
                      {{ selectNodeData.name }}
                    </div>
                    <div slot="head-operation" class="head-operation">
                      <HeadOperationItem
                        v-show="selectNodeData.id"
                        name="addParams"
                        @onOprationItem="togglePage"
                      >
                        <i slot="operation-icon" class="icon-add"></i>
                        <span slot="operation-name">{{
                          PLC_PARAMS.ADD_PARAMS
                        }}</span>
                      </HeadOperationItem>
                      <HeadOperationItem
                        v-show="selectNodeData.id"
                        name="addDevice"
                        @onOprationItem="exportData"
                      >
                        <i slot="operation-icon" class="icon-export"></i>
                        <span slot="operation-name">{{ BTN_TEXT.EXPORT }}</span>
                      </HeadOperationItem>
                    </div>
                  </HeaderTitle>
                  <VXETable
                    id="table"
                    :tableName="''"
                    :height="$options.filters.getTableHeight(190)"
                    :tableLoading="tableLoading"
                    :pager-config="tablePage"
                    :tableDatas="tableDatas"
                    :tableColumns="tableColumns"
                    :tablePage="tablePage"
                    @onTablePageChange="onTablePageChange"
                    @chooseCheck="tableChooseCheck"
                    ref="xGrid"
                  >
                    <div slot="tableOperation" class="tableOperations">
                      <div class="tableOperation-item">
                        <HeadOperationItem
                          name="batchDeleteStaff"
                          @onOprationItem="deteleAll()"
                        >
                          <i slot="operation-icon" class="icon-batchDelete"></i>
                          <span slot="operation-name">{{
                            BTN_TEXT.BATCH_DELETE
                          }}</span>
                        </HeadOperationItem>
                        <HeadOperationItem
                          name="batchDeleteStaff"
                          @onOprationItem="warningAll()"
                        >
                          <i slot="operation-icon" class="icon-batchDelete"></i>
                          <span slot="operation-name">{{
                            PLC_PARAMS.BATCH_WARN
                          }}</span>
                        </HeadOperationItem>
                      </div>
                    </div>
                  </VXETable>
                </div>
              </template>
            </div>
          </div>
        </el-main>

        <el-dialog
          :class="[$store.state.dialogMarginClass]"
          :title="titlename"
          :width="'500px'"
          :visible.sync="dialogVisible"
        >
          <el-form ref="typeForm" :model="form" :rules="rules">
            <el-form-item
              :label="PLC_PARAMS.PARAMETER_NAME_CN"
              :label-width="formLabelWidth"
              prop="cnName"
            >
              <el-input v-model="form.cnName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.PARAMETER_NAME_EN"
              :label-width="formLabelWidth"
              prop="enName"
            >
              <el-input v-model="form.enName" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button  @click="dialogVisible = false">{{
              BTN_TEXT.CANCEL
            }}</el-button>
            <el-button type="primary" @click="submit">{{ BTN_TEXT.CONFIRM }}</el-button>
          </span>
        </el-dialog>

        <el-dialog
          :class="[$store.state.dialogMarginClass]"
          :title="titleParamName"
          :width="'500px'"
          :visible.sync="dialogVisibleParam"
        >
          <el-form
            ref="paramForm"
            class="paramForm"
            :model="formParam"
            :rules="rulesParam"
          >
            <el-form-item
              :label="PLC_PARAMS.ZH_NAME"
              :label-width="formLabelWidth"
              prop="cnName"
            >
              <el-input
                v-model="formParam.cnName"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.PARAMETER_NAME"
              :label-width="formLabelWidth"
              prop="parameterName"
            >
              <el-input
                v-model="formParam.parameterName"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.EN_NAME"
              :label-width="formLabelWidth"
              prop="enName"
            >
              <el-input
                v-model="formParam.enName"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.SHORT_NAME"
              :label-width="formLabelWidth"
              prop="shortName"
            >
              <el-input
                v-model="formParam.shortName"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.UNIT"
              :label-width="formLabelWidth"
              prop="unit"
            >
              <el-input v-model="formParam.unit" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.IS_WARNING"
              :label-width="formLabelWidth"
              prop="isWarning"
            >
              <el-select
                v-model="formParam.isWarning"
                :placeholder="MSG.PLEASE_CHOOSE"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.VALUE_TYPE"
              :label-width="formLabelWidth"
              prop="valueType"
            >
              <el-select
                v-model="formParam.valueType"
                :placeholder="MSG.PLEASE_CHOOSE"
              >
                <el-option label="int" value="int"></el-option>
                <el-option label="double" value="double"></el-option>
                <el-option label="String" value="String"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.DESCRIPTION"
              :label-width="formLabelWidth"
            >
              <el-input
                v-model="formParam.description"
                autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              :label="PLC_PARAMS.REMARK"
              :label-width="formLabelWidth"
            >
              <el-input
                v-model="formParam.remark"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleParam = false">{{
              BTN_TEXT.CANCEL
            }}</el-button>
            <el-button type="primary" @click="submitParam">{{ BTN_TEXT.CONFIRM }}</el-button>
          </span>
        </el-dialog>

        <v-contextmenu ref="contextmenu">
          <v-contextmenu-item
            v-if="rightMenu.indexOf(1) > -1"
            @click="showDialog(1)"
            >{{ BTN_TEXT.ADD_BORTHER }}</v-contextmenu-item
          >
          <v-contextmenu-item
            v-if="rightMenu.indexOf(1) > -1 && rightMenu.indexOf(2) > -1"
            divider
          ></v-contextmenu-item>
          <v-contextmenu-item
            v-if="rightMenu.indexOf(2) > -1"
            @click="showDialog(2)"
            >{{ BTN_TEXT.ADD_CHILDREN }}</v-contextmenu-item
          >
          <v-contextmenu-item
            v-if="rightMenu.indexOf(2) > -1 && rightMenu.indexOf(3) > -1"
            divider
          ></v-contextmenu-item>
          <v-contextmenu-item
            v-if="rightMenu.indexOf(3) > -1"
            @click="showDialog(3)"
            >{{ BTN_TEXT.MODIFY }}</v-contextmenu-item
          >
          <v-contextmenu-item
            v-if="rightMenu.indexOf(3) > -1 && rightMenu.indexOf(4) > -1"
            divider
          ></v-contextmenu-item>
          <v-contextmenu-item
            v-if="rightMenu.indexOf(4) > -1"
            @click="deleteCurrentNode"
            >{{ BTN_TEXT.DELETE }}</v-contextmenu-item
          >
        </v-contextmenu>
      </el-container>
    </div>
    <div v-if="showPage === 1">
      <addFatherDictionary
        :typeId="selectNodeData.id"
        :typeName="selectNodeData.name"
        @goBack="goBack"
      >
      </addFatherDictionary>
    </div>
    <div v-else-if="showPage === 2">
      <addChildrenDictionary
        :fatherNodeData="fatherNodeData"
        :typeId="selectNodeData.id"
        :typeName="selectNodeData.name"
        @goBack="goBack"
      ></addChildrenDictionary>
    </div>
  </div>
</template>

<script>
import js from '@/assets/js/plc/DataDictionary/index';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/plc/DataDictionary/index.less';
</style>
