// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 导入所需接口
import { dataDictionaryInsertAndRelationshipAction } from '@/api/management';
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, PLC_PARAMS } from '@/utils/constantsFile';
export default {
  components: {
    // 加载标题组件
    HeaderTitle
  },
  props: {
    // 模板名称
    typeName: {
      type: String,
      default: ''
    },
    // 类型id
    typeId: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      BTN_TEXT: BTN_TEXT,
      // 页面标题
      titleName: PLC_PARAMS.ADD_PARAMS,
      // 表单验证
      validRules: {
        cnName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        parameterName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        enName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        shortName: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        unit: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT }],
        // description: [{ required: true, message: '请填写描述' }],
        // remark: [{ required: true, message: '请填写备注' }],
        isWarning: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }],
        valueType: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }]
      },
      // 可编辑配置
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: true
        // icon: 'fa fa-file-text-o'
      },
      // table分页配置
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 20,
        align: 'left',
        pageSizes: [5, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 使用自定义插槽
          left: 'pager_left'
        }
      },
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'cnName',
          title: PLC_PARAMS.ZH_NAME,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'parameterName',
          title: PLC_PARAMS.PARAMETER_NAME,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'enName',
          title: PLC_PARAMS.EN_NAME,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'shortName',
          title: PLC_PARAMS.SHORT_NAME,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'unit',
          title: PLC_PARAMS.UNIT,
          align: 'center',
          editRender: { name: 'input' }
        },

        {
          field: 'isWarning',
          title: PLC_PARAMS.IS_WARNING,
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              { label: VARIABLE.YES, value: 1 },
              { label: VARIABLE.NO, value: 0 }
            ]
          }
        },
        {
          field: 'valueType',
          title: PLC_PARAMS.VALUE_TYPE,
          align: 'center',
          editRender: {
            name: '$select',
            options: [
              { label: 'int', value: 'int' },
              { label: 'double', value: 'double' },
              { label: 'String', value: 'String' }
            ]
          }
        },
        {
          field: 'description',
          title: PLC_PARAMS.DESCRIPTION,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: 'remark',
          title: PLC_PARAMS.REMARK,
          align: 'center',
          editRender: { name: 'input' }
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '200',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <el-button onClick={() => this.addRow(row, column)} type="text">
                  {BTN_TEXT.INSERT_ONE_ROW}
                </el-button>,
                <el-button onClick={() => this.deleteRow(row, column)} type="text">
                  {BTN_TEXT.DELETE}
                </el-button>
              ];
            }
          }
        }
      ]
    };
  },
  computed: {},
  created() { },
  mounted() {
    // 默认新增一行
    this.insertEvent();
  },
  methods: {
    /**
     * @description: 点击新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    addRow(row, column) {
      const index = this.$refs.xGrid.$getRowIndex(row);
      this.insertEvent(this.tableDatas[index + 1]);
    },
    /**
     * @description: 移除一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    deleteRow(row, column) {
      this.$XModal.confirm(MSG.CONFIRM_DELETE).then(type => {
        if (type === 'confirm') {
          this.$refs.xGrid.remove(row);
        }
      });
    },
    /**
     * @description: 执行新增一行
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    async insertEvent(row) {
      const record = {
        isWarning: null,
        valueType: null
      };
      // 往表格插入临时数据，从指定位置插入一行或多行
      await this.$refs.xGrid.insertAt(record, row);
    },
    /**
     * @description: 表单提交
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submit() {
      try {
        const {
          // 新增的数据
          insertRecords
        } = this.$refs.xGrid.getRecordset();
        const res = await dataDictionaryInsertAndRelationshipAction(
          {
            deviceTypeId: this.typeId
          },
          insertRecords);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 返回主页面
          this.goBack();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async fullValidEvent() {
      const errMap = await this.$refs.xGrid
        .fullValidate()
        .catch(errMap => errMap);
      if (errMap) {
        var msgList = [];
        Object.values(errMap).forEach(errList => {
          errList.forEach((params, index) => {
            const { column, rules } = params;
            rules.forEach(rule => {
              msgList.push(
                `${VARIABLE.THE} ${index + 1} ${VARIABLE.ROW} ${column.title} ${MSG.VALIDATION_ERROR}：${rule.message}`
              );
            });
          });
        });
        this.$XModal.alert({
          status: 'error',
          message: () => {
            return [
              <div class="red" style="max-height: 400px;overflow: auto;">
                {msgList.map(msg => (
                  <div>{msg}</div>
                ))}
              </div>
            ];
          }
        });
      } else {
        // 通过验证，执行提交
        this.submit();
      }
    },
    /**
     * @description: 返回按钮点击
     * @param 无
     * @return:无
     * @author: ZJM
     */
    goBack() {
      this.$emit('goBack', VARIABLE.ZERO_NUMBER);
    }
  }
};
