<template>
  <el-container class="content">
    <el-header>
      <HeaderTitle class="header-title" :titleName="titleName">
        <div slot="head-search" class="head-search">{{typeName}}</div>
      </HeaderTitle>
    </el-header>
    <el-main>
      <div class="area-main">
          <vxe-grid
            border="full"
            resizable
            keep-source
            ref="xGrid"
            class="vxe-table-element"
            height="550"
            :edit-rules="validRules"
            :columns="tableColumns"
            :data="tableDatas"
            :edit-config="{trigger: 'click', mode: 'row', showStatus: true, icon: 'fa fa-file-text-o'}"
          ></vxe-grid>
          <div class="btnDiv">
            <el-button type="primary" @click="fullValidEvent">{{BTN_TEXT.SAVE}}</el-button>
            <el-button @click="goBack">{{BTN_TEXT.RETURN}}</el-button>
          </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/plc/DataDictionary/addFatherDictionary';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/plc/DataDictionary/addFatherDictionary.less';
</style>
