    /**
     * @description: 点击参数修改
     * @param: row 行数据
     * @return: 无
     * @author: ZJM
     */
    setUpdate(row) {
      this.formParam = JSON.parse(JSON.stringify(row));
      this.dialogVisibleParam = true;
    },
    /**
     * @description: 提交修改数据
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submitParam() {
      // 执行验证
      this.$refs.paramForm.validate(valid => {
        // 若验证通过
        if (valid) {
          this.submitParamFunc();
        }
      });
    },
    /**
     * @description: 提交修改数据接口
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async submitParamFunc() {
      try {
        const res = await dataDictionaryUpdateByIdAction(this.formParam);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.dialogVisibleParam = false;
          this.findList();
          // 提示正常信息
          showSuccessMessage(res.message);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
